.table-container {
  display: flex;
  margin: 0 auto;
  width: 95%;
  overflow: auto; }
  .table-container table {
    align-self: center;
    width: 100%;
    height: 100%;
    border-collapse: collapse;
    box-shadow: 0.5px 2px 5px rgba(223, 223, 223, 0.897); }
    .table-container table * {
      border-radius: 2px; }
    .table-container table thead th {
      height: 30px;
      text-align: left;
      padding-left: 15px;
      font-weight: 500;
      cursor: pointer;
      font-size: 14px; }
    .table-container table thead .header-icon {
      font-size: 12px;
      float: right;
      opacity: 0.3;
      padding-top: 4px; }
    .table-container table tbody {
      font-size: 13px; }
      .table-container table tbody tr {
        cursor: pointer;
        height: 35px;
        font-weight: 400;
        -webkit-transition: all linear 0.15s;
        transition: all linear 0.15s;
        position: relative; }
        .table-container table tbody tr:hover::after {
          content: '';
          position: absolute;
          left: 15px;
          margin-top: 10px;
          width: 0;
          height: 0;
          border-top: 6px solid transparent;
          border-bottom: 6px solid transparent;
          border-left: 6px solid #8a8a8a; }
        .table-container table tbody tr:nth-of-type(2n + 1) {
          background-color: rgba(249, 249, 249, 0.904);
          border-top: 2px solid #d8d5d5;
          border-bottom: 2px solid #d8d5d5;
          padding-left: 15px; }
        .table-container table tbody tr td {
          padding-left: 10px; }
      .table-container table tbody .popup-content {
        width: 37% !important;
        padding: 0 !important;
        border: none !important; }
        .table-container table tbody .popup-content .modal {
          -webkit-transition: -webkit-transform 0.75s;
          transition: -webkit-transform 0.75s;
          transition: transform 0.75s;
          transition: transform 0.75s, -webkit-transform 0.75s;
          background-color: #fafafa;
          box-shadow: 4px 4px 15px rgba(0, 0, 0, 0.2);
          color: #080808;
          border-radius: 5px;
          padding: 7px; }
          .table-container table tbody .popup-content .modal > .header {
            color: #8a8a8a;
            width: 98%;
            margin-left: 5px;
            border-bottom: 1px solid gray;
            font-size: 18px;
            text-align: center;
            line-height: 50px; }
          .table-container table tbody .popup-content .modal > .content {
            display: flex;
            flex-direction: column;
            flex-basis: auto;
            border-radius: 10%;
            padding-left: 5px; }
            .table-container table tbody .popup-content .modal > .content * {
              margin: 4px 0;
              font-size: 15px;
              color: #8a8a8a; }
            .table-container table tbody .popup-content .modal > .content > textarea {
              height: 100px;
              max-width: 98%; }
          .table-container table tbody .popup-content .modal > .close {
            padding-right: 10px;
            padding-top: 5px;
            font-size: 16px;
            width: 10px;
            height: 10px;
            float: right;
            cursor: pointer;
            color: #8a8a8a; }

